* {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: white;
}

body {
    background-color: rgb(29, 29, 29);
}

#root {
    display: grid;
    justify-items: center;
}

.name {
    font-family: 'Montserrat', 'Trebuchet MS', sans-serif;
    text-align: center;
    font-size: 40pt;
    color: orange;
    margin: 15px;
}

.app {
    width: auto;
}

.input {
    padding: 10px;
    background-color: rgb(43, 43, 43);
    border-radius: 50px;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 3fr 1fr 1fr;
    font-size: 45pt;
}

input[type='text'] {
    outline: none;
    background: none;
    margin: 0 10px 0 5px;
    padding: 0 10px;
    border: none;
    border-radius: 8px;
    border-bottom: 2px solid grey;
    font-weight: bold;
    transition: border-bottom-color 0.25s ease-in-out, background-color 0.25s ease-in-out;
}

input[type='text']:hover {
    background-color: rgb(53, 53, 53);
}

input[type='text']:focus, input[type='text']:hover {
    border-bottom-color: white;
}

input[type='checkbox'] {
    width: 1.25em;
    height: 1.25em;
}

button {
    outline: none;
    border: none;
    background-color: rgb(43, 43, 43);
    font-weight: bold;
    border-radius: 25px;
    padding: 10px;
    transition: background-color 0.25s ease-in-out;
}

button:hover {
    background-color: rgb(53, 53, 53);
}

label {
    margin: none;
    margin-left: 15px;
    font-weight: bolder;
    font-size: 13pt;
    padding: none;
}

.todo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(43, 43, 43);
    border-radius: 50px;
    padding: 5px;
    margin: 5px;
    transition: transform 0.25s ease-in-out;
}

.todo:hover {
    transform: scale(103%);
}

.delete:hover {
    background-color: red;
}

.todo div {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.remaining {
    margin: 10px;
    color: white;
    font-weight: bolder;
}

::-webkit-scrollbar {
    width: 10pt;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: rgb(43, 43, 43);
    border-radius: 10px;
}